.page {
  color: #fff;
  scroll-behavior: smooth;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 19.5px;
}

.page:has(.page__aside:target) {
  overflow: hidden;
}

.page__body {
  background-color: #f2f6fa;
  margin: 0;
}

.page__aside {
  z-index: 3;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  transform: translateX(-100%);
}

.page__aside:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.header {
  background-color: #2c2c2c;
  min-width: 320px;
}

.header__content {
  box-sizing: border-box;
  grid-template-columns: repeat(2, 130px);
  justify-content: center;
  align-items: end;
  column-gap: 20px;
  height: 96px;
  padding-top: 18px;
  padding-bottom: 48px;
  display: grid;
}

@media (width >= 640px) {
  .header__content {
    grid-template-columns: repeat(6, 68px);
    justify-content: center;
    column-gap: 30px;
    display: grid;
  }
}

@media (width >= 1024px) {
  .header__content {
    grid-template-columns: repeat(12, 58px);
    justify-content: center;
    column-gap: 20px;
    display: grid;
  }
}

@media (width >= 1600px) {
  .header__content {
    grid-template-columns: repeat(12, 68px);
    justify-content: center;
    column-gap: 30px;
    display: grid;
  }
}

