    height: 104px;
    padding-bottom: 56px;
  }
}

@media (width >= 1024px) {
  .header__content {
    align-items: center;
    height: 120px;
    padding: 0;
  }
}

.header__icons {
  grid-column: 1;
  justify-self: start;
}

@media (width >= 640px) {
  .header__icons {
    grid-column: span 2;
